export default {
  data: () => ({
    valid: true,
    name: "",
    anony: false,
    concern: "",
    concernRules: [(v) => !!v || "This field is required"],
    whyConcern: "",
    whyConcernRules: [
      (v) => !!v || "This field is required",
      (v) => (v && v.length >= 50) || "it must be at least 50 characters",
    ],
  }),

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.$router.push({
          path:
            "thankyou/?name=" +
            this.name +
            "&concern=" +
            this.concern +
            "&why=" +
            this.whyConcern +
            "&anon=" +
            this.anony,
        });
      }
    },
  },
};
